/* Lightbox */
:root {
  --timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  --animation-duration: 250ms;
}

.is-hidden {
  overflow-y: hidden;
}

.lightbox {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;

  opacity: 0;
  pointer-events: none;
  overflow-y: scroll;

  background: rgba(32, 13, 29, 0.6);
  backdrop-filter: blur(3px);
  transition: opacity var(--animation-duration) var(--timing-function);

  &.is-open {
    opacity: 1;
    pointer-events: initial;
  }
}

