.lightbox__container {
  position: relative;
  top: 0%;
  left: 0%;
  transform: translate(-50%, -50%);
  padding-top: 75px;
  padding-bottom: 100px;
  margin-top: 100px;
  margin-bottom: 100px;
  box-sizing: border-box;

  background-color: $card-body-color;
  border-radius: 150px 0px;
  transform: scale(0.95);
  transition: transform var(--animation-duration) var(--timing-function);

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 650px;
    max-height: 831px;
    padding-top: 28px;
    padding-bottom: 56px;
    padding-right: 20px;
    padding-left: 20px;
    margin-top: 150px;
    margin-bottom: 150px;
  }

  @media screen and (min-width: 1280px) {
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 930px;
    max-height: 1023px;
    padding-top: 62px;
    padding-bottom: 62px;
    padding-right: 20px;
    padding-left: 20px;
  }
}

.lightbox.is-open .lightbox__content {
  transform: scale(1);
}
.lightbox__button {
  background-color: #fff2fd;
}

.modal-small-picto {
  display: block;
  margin-bottom: 18px;
  margin-right: auto;
  margin-left: auto;
  width: 102px;
  height: 102px;
  border-radius: 50%;
  object-fit: cover;

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    margin-bottom: 40px;
    width: 70px;
    height: 70px;
  }

  @media screen and (min-width: 1280px) {
    width: 132px;
    height: 132px;
  }
}

.modal-detalis-event {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 320px;
  margin: 25px auto;

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 595px;
    margin: 0px auto;
  }

  @media screen and (min-width: 1280px) {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 860px;
    margin: 0px auto;
  }
}

.poster {
  display: none;

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    display: block;
    width: 233px;
    height: 416px;
    margin-right: 30px;
  }

  @media screen and (min-width: 1280px) {
    display: block;
    width: 360px;
    height: 460px;
    margin-right: 30px;
  }
}

.poster img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.modal-info-event {
  display: flex;
  flex-direction: column;
  align-items: center;

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    display: flex;
    flex-direction: column;
    align-items: normal;
    weight: 265px;
  }

  @media screen and (min-width: 1280px) {
    display: flex;
    align-items: normal;
    weight: 450px;
  }
}

.info-event-title {
  @extend %event-name-text;
  display: block;
  margin-bottom: 5px;
  text-align: left;
}

.info-event-text {
  @extend %event-date-text;
  color: #000;
  display: block;
  margin-bottom: 20px;
  text-align: left;
}

.price-span {
  display: block;
  align-items: center;
}

.first-symbol-text::first-letter {
  text-transform: uppercase;
}

.price-barcode {
  margin-right: 13px;

  width: 29px;
  height: 19px;

  @media screen and (max-width: 1279px) {
    width: 24px;
    height: 16px;
  }
}
.lightbox__button.link {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;

  width: 145px;
  height: 40px;
  margin-bottom: 30px;

  font-family: Montserrat;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.25;
  color: #ffffff;

  background: #4c00fe;
  border-radius: 5px;

  &:hover,
  &:focus {
    background: #2e009a;
  }
}

.button-more-event {
  @extend %modal-btn-text;
  display: block;
  height: 40px;
  margin-left: auto;
  margin-right: auto;

  border: 1px solid #4c00fe;
  box-sizing: border-box;
  border-radius: 5px;
  color: #4c00fe;

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    display: block;
    height: 40px;
    margin-top: 40px;
  }

  @media screen and (min-width: 1280px) {
    display: block;
    height: 40px;
    margin-top: 40px;
  }

  &:hover,
  &:focus {
    border: 1px solid #dc56c5;
  }
}

.button-more-close {
  position: absolute;
  top: 8px;
  right: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  border: none;
  color: #fff2fd;

  &:hover,
  &:focus {
    fill: #2196f3;
  }

  background-image: url('../images/close.svg');
  background-position: center;
  background-size: 60%;
  background-repeat: no-repeat;
}
