@import './utils/placeholders';

.events > .container {
  padding-bottom: 60px;

  @media screen and (min-width: $desktop) {
    padding-bottom: 80px;
  }
}

.events-gallery {
  margin-bottom: 60px;

  @media screen and (min-width: $desktop) {
    margin-bottom: 80px;
  }
}

.event-card {
  display: block;
  width: 128px;
  min-height: 231px;
  margin-bottom: 33px;
  position: relative;
  cursor: pointer;
  transition: $timing-function;

  @media (min-width: $tablet) {
    width: 194px;
    min-height: 312px;
  }

  @media (min-width: $desktop) {
    width: 196px;
    min-height: 312px;
  }
  -webkit-animation: bounce-in-top 1.1s 1s both;
  animation: bounce-in-top 1.1s 1s both;
}
@-webkit-keyframes bounce-in-top {
  0% {
    -webkit-transform: translateY(-500px);
    transform: translateY(-500px);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
    opacity: 0;
  }
  38% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
    opacity: 1;
  }
  55% {
    -webkit-transform: translateY(-65px);
    transform: translateY(-65px);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }
  72% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
  }
  81% {
    -webkit-transform: translateY(-28px);
    transform: translateY(-28px);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }
  90% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
  }
  95% {
    -webkit-transform: translateY(-8px);
    transform: translateY(-8px);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
  }
}
@keyframes bounce-in-top {
  0% {
    -webkit-transform: translateY(-500px);
    transform: translateY(-500px);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
    opacity: 0;
  }
  38% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
    opacity: 1;
  }
  55% {
    -webkit-transform: translateY(-65px);
    transform: translateY(-65px);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }
  72% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
  }
  81% {
    -webkit-transform: translateY(-28px);
    transform: translateY(-28px);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }
  90% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
  }
  95% {
    -webkit-transform: translateY(-8px);
    transform: translateY(-8px);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
  }
}

.event-card:hover {
  transform: scale(1.05);
}

.event-card:hover .img-trumb {
  box-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #fff, 0 0 20px $main-pulsar-color,
    0 0 15px $main-pulsar-color, 0 0 30px $main-pulsar-color, 0 0 20px $main-pulsar-color,
    0 0 5px $main-pulsar-color;
  animation-name: pulsate;
  animation-duration: 1000ms;
  animation-timing-function: ease-out;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

@keyframes pulsate {
  0% {
    box-shadow: 0 0 1px $main-text-color, 0 0 2px $main-text-color, 0 0 3px $main-text-color,
      0 0 4px $main-pulsar-color, 0 0 3px $main-pulsar-color, 0 0 6px $main-pulsar-color,
      0 0 4px $main-pulsar-color, 0 0 1px $main-pulsar-color;
  }
  100% {
    box-shadow: 0 0 5px $main-text-color, 0 0 10px $main-text-color, 0 0 15px $main-text-color,
      0 0 20px $main-pulsar-color, 0 0 15px $main-pulsar-color, 0 0 30px $main-pulsar-color,
      0 0 20px $main-pulsar-color, 0 0 5px $main-pulsar-color;
  }
}

@keyframes pulsate-text {
  0% {
    text-shadow: 0 0 1px $main-text-color, 0 0 2px $main-text-color, 0 0 3px $main-text-color,
      0 0 4px $main-pulsar-color, 0 0 3px $main-pulsar-color, 0 0 6px $main-pulsar-color,
      0 0 4px $main-pulsar-color, 0 0 1px $main-pulsar-color;
  }
  100% {
    text-shadow: 0 0 5px $main-text-color, 0 0 10px $main-text-color, 0 0 15px $main-text-color,
      0 0 20px $main-pulsar-color, 0 0 15px $main-pulsar-color, 0 0 30px $main-pulsar-color,
      0 0 20px $main-pulsar-color, 0 0 5px $main-pulsar-color;
  }
}

.event-card:hover .event-name {
  color: $main-body-color;
  text-shadow: $hero-text-color 0px 0px 5px, $hero-text-color 0px 0px 10px,
    $hero-text-color 0px 0px 15px, $hero-text-color 0px 0px 20px;
  animation-name: pulsate-text;
  animation-duration: 1000ms;
  animation-timing-function: ease-out;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

.rose-border {
  position: absolute;
  width: 109px;
  height: 101px;
  right: 0px;
  top: -9px;

  border: 1px solid $rose-border-color;
  border-radius: 50px 0px;

  @media (min-width: $tablet) {
    width: 153px;
    height: 143px;
  }

  @media (min-width: $desktop) {
    width: 153px;
    height: 143px;
  }
}

.img-trumb {
  height: 161px;
  width: 128px;
  margin-left: auto;
  margin-right: auto;
  border-radius: 50% 0 50% 0;
  overflow: hidden;
  transition: $timing-function;

  @media (min-width: $tablet) {
    height: 227px;
    width: 180px;
  }

  @media (min-width: $desktop) {
    width: 180px;
    height: 227px;
  }
}

.img-trumb > img {
  display: block;
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.event-info {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  // width: 138px;
  padding: 10px;
}

.event-name {
  @extend %event-name-text;
  margin-bottom: 5px;

  @media (min-width: $tablet) {
    font-size: 16px;
    line-height: 20px;
  }
}

.event-data {
  @extend %event-date-text;
  margin-bottom: 5px;

  @media (min-width: $tablet) {
    font-size: 16px;
    line-height: 20px;
  }
}

.location {
  @extend %event-place-text;
  margin-bottom: 5px;

  @media (min-width: $tablet) {
    font-size: 14px;
    line-height: 17px;
  }
}

.location::before {
  // content: url(../images/symbol-defs.svg#g-tag);
  content: url(../images/geo.png);
  fill: $main-text-color;
  display: inline-block;
  width: 7px;
  height: 10px;
  margin-right: 3px;
}

.error-text {
  @extend %event-name-text;
  margin-bottom: 5px;
  flex-grow: 1;

  @media (min-width: $tablet) {
    font-size: 16px;
    line-height: 20px;
  }
}

